.footer-general {
  max-width: 1107px;
  margin: 0 auto 20px;
}

.footer-general .footer-container {
  max-width: 785px;
}

footer .newsletter-box {
  font-family: 'Roboto Condensed', sans-serif;
  font-weight: bold;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  background-color: #F7F7F7;
  height: 111px;
  padding: 10px;
  border-top: 1px solid var(--gray);
  border-bottom: 1px solid var(--gray);
  box-sizing: border-box;
  max-width: 765px;
  margin: 0 auto;
}

footer .newsletter-box .title {
  color: #191919;
  font-size: 14px;
  margin: 0;
}

footer .newsletter-box .subtitle {
  font-family: 'Merriweather', serif;
  color: #9B9B9B;
  font-size: 12px;
  margin: 3px 0;
}

footer .newsletter-form {
  display: flex;
  justify-content: space-between;
}

footer .newsletter-form input {
  font-family: 'Roboto Condensed', sans-serif;
  color: var(--medium-gray);
  border: 1px solid var(--gray);
  font-size: 14px;
  width: 171px;
  height: 40px;
  padding: 0 10px;
  border-radius: 4px;
  font-weight: bold;
}

footer .newsletter-form .newsletter-button {
  background-color: var(--orange);
  color: var(--white);
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 14px;
  border-radius: 4px;
  width: 120px;
  height: 40px;
  cursor: pointer;
}

footer .share {
  font-family: 'Roboto', sans-serif;
  display: flex;
  flex-direction: column;
  align-items: center;
  height: 200px;
}

footer .share .social-media {
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-family: 'Roboto', sans-serif;
  font-size: 12px;
  display: flex;
  width: 184px;
  margin: 20px 0;
}

footer .share .comments {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  font-weight: bold;
}

footer .share .comments .comments-button {
  border-radius: 4px;
  font-size: 14px;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: var(--blue);
  color: var(--white);
  width: 210px;
  height: 32px;
}

footer .share .comments p {
  font-size: 12px;
}

footer span a {
  text-decoration: underline;
}